$rating-width: 250px;
$rating-height: 50px;

.rating {
  font-size: 0;
  white-space: nowrap;
  display: inline-block;
  width: $rating-width;
  height: $rating-height;
  position: relative;
  background: url('data: image/svg+xml;base64,');
  background-size: contain;

  i {
    opacity: 0;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 20%;
    z-index: 1;
    background: url('data: image/svg+xml;base64,');
    background-size: contain;
  }

  input {
    opacity: 0;
    display: inline-block;
    width: 20%;
    height: 100%;
    margin: 0;
    padding: 0;
    z-index: 2;
    position: relative;

    &:hover + i,
    &:checked + i{
      opacity: 1;
    }
  }

  i ~ i{
    width: 40%;
  }

  i ~ i ~ i{
    width: 60%;
  }

  i ~ i ~ i ~ i{
    width: 80%;
  }

  i ~ i ~ i ~ i ~ i{
    width: 100%;
  }

  .form__error {
    display: block;
  }
}
